@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@600;800&family=Press+Start+2P&family=Roboto:ital,wght@0,400;0,700;1,400;1,700&family=Space+Mono:wght@400;700&display=swap");html{font-family:'Roboto', sans-serif;font-size:16px;word-spacing:1px;-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;box-sizing:border-box;margin:0}body{background-color:#251f68;color:#eee}h1,h2,h3,h4,h5,h6,strong{color:#fff}.wrapper{display:flex;padding:40px 0}.container{width:100%;max-width:1140px;margin:0 auto}@media (max-width: 1180px){.container{padding:0 20px}}.logo{font-weight:800;font-family:'Montserrat', sans-serif;line-height:1}label{display:inline-block}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none}input[type=number]{-moz-appearance:textfield}.input-group.is-invalid .btn{border-color:#fd1874}
